.read-more-state:checked ~ .read-more-wrap .read-more-target {
    
      opacity: 1;
      max-height: 999em;

      font-size: 16px;
      line-height: 24px;

    @include media-breakpoint-up (sm) {
        font-size: 26px;
        line-height: 34px;
    }

    @include media-breakpoint-up (lg) {
        font-size: 16px;
        line-height: 24px;
    }

}

body, html {
    font-family: $body-font;
    background: white;
}

main { overflow: hidden; }

.mm-wrapper { overflow: unset; }

/* let's define variable according to the style guide */

h1, h2, h3 {
    font-family: $heading-font;
    margin: 0;
}

h1, .h1{
    font-size: 35px;
    line-height: 1.05;
    font-weight: 800;
    color: text-color("dark");

    @include media-breakpoint-up (xxs) {
        font-size: 35px;
        line-height: 1.2;
    }

    @include media-breakpoint-up (xs) {
        font-size: 50px;
        line-height: 1.2;
    }

    @include media-breakpoint-up (sm) {
        font-size: 60px;
        line-height: 1.2;
    }

    @include media-breakpoint-up (md) {
        font-size: 70px;
        line-height: 1.2;
    }

    @include media-breakpoint-up (lg) {
        font-size: 40px;
        line-height: 1.25;
    }

    @include media-breakpoint-up(xl) {
        font-size: 60px;
        line-height: 1.17;     
    }    

}

h2, .h2 {
    font-size: 24px;
    line-height: 1.25;
    letter-spacing: 0.27px;
    color: text-color("dark");  
    font-weight: 800;
    text-transform: uppercase;

    @include media-breakpoint-up(sm) {
        font-size: 48px;
        line-height: 1.1;
    }

    @include media-breakpoint-up(lg) {
        font-size: 35px;
        line-height: 1.17;
    }

    @include media-breakpoint-up(xl) {
        font-size: 40px;
        line-height: 1.17;        
    }    
}

h3, .h3 {
    font-size: 22px;
    line-height: 1.1;
    font-weight: 700;
    color: text-color("dark"); 

    @include media-breakpoint-up (sm) {
        font-size: 48px;
        line-height: 1.1;  
        font-weight: bold; 
    }

    @include media-breakpoint-up(lg) {
        font-size: 32px;
        line-height: 1.2;    
    }
}

.body-bold{
    font-weight: bold;
    font-size: 16px;
    line-height: 20px;
    font-family: $body-font;

    @include media-breakpoint-up (sm) {
        font-size: 36px;
        line-height: 44px;
    }

    @include media-breakpoint-up(lg) {
        font-size: 24px;
        line-height: 28px;
    }
}

.big-body {
    font-family: $body-font;
    font-size: 16px;
    line-height: 20px;

    @include media-breakpoint-up (sm) {
        font-size: 36px;
        line-height: 44px;
    }

    @include media-breakpoint-up(lg) {
        font-size: 24px;
        line-height: 32px;
    }
}

.small-body-bold {
    font-size: 16px;
    font-weight: bold;
    line-height: 1.27;

    @include media-breakpoint-up (sm) {
        font-size: 32px;
        line-height: 1.2;
    }

    @include media-breakpoint-up(lg) {
        font-size: 22px;
        line-height: 1.15;  
    }
}

.small-body {
    color: #000;
    font-size: 16px;
    line-height: 22px;

    @include media-breakpoint-up (sm) {
        font-size: 32px;
        line-height: 1.25;
    }

    @include media-breakpoint-up(lg) {
        font-size: 18px;
        line-height: 1.25;
    }

    @include media-breakpoint-up(xl) {
        font-size: 22px;
        line-height: 1.27;  
    }
}
 
p {
    font-size: 16px;
    line-height: 24px;

    @include media-breakpoint-up (sm) {
        font-size: 26px;
        line-height: 34px;
    }

    @include media-breakpoint-up (lg) {
        font-size: 16px;
        line-height: 24px;
    }
}

.text-cta {
    font-size: 15px;
    line-height: 1.1;    
    letter-spacing: 0.1em;

    @include media-breakpoint-up (sm) {
        font-size: 30px;
        line-height: 1.2;
    }

    @include media-breakpoint-up (lg) {
        font-size: 16px;
    }
    
}

.date {

    font-size: 12px;
    line-height: 1.1;    

    @include media-breakpoint-up (sm) {
        font-size: 24px;
        line-height: 1.2;
    }

    @include media-breakpoint-up (lg) {
        font-size: 14px;
    }

}

.text-tag {

    font-size: 12px;
    line-height: 1.1;    
    letter-spacing: 0.1em;

    @include media-breakpoint-up (sm) {
        font-size: 24px;
        line-height: 1.2;
    }

    @include media-breakpoint-up (lg) {
        font-size: 14px;
    }

}

.btn {
    font-size: 16px;
    letter-spacing: 1.5px;
    text-transform: uppercase;
    line-height: 1.25;
    padding: 20px;
    font-family: $body-font;
    font-weight: bold;
    width: 100%;
    border: 8px;

    @include media-breakpoint-up (sm) {
        font-size: 30px;
        padding: 20px;
    }

    @include media-breakpoint-up(lg) {
        font-size: 16px;
        width: 366px;
    }

}

.btn-outline {
    border: 1px solid $gray;
    color: $black;

    &:hover {
        background-color: rgba($color: $gray, $alpha: 0.1);
    }
}

@media (min-width: 1600px) {
    .container {
        max-width: 1536px;
        padding-left: 15px;
        padding-right: 15px;

    }
}

.text-light {
    h1, h2, h3, p, ul, li {
        color: text-color("light");
    }
}

.text-light-gray {
    color: rgba( $gray, 0.6);
}

.read-more-btn {
    font-size: 16px;
    font-weight: bold;
    letter-spacing: 1.5px;
    line-height: 1.125;
    text-decoration: none;
    text-transform: uppercase;

    svg {
        position: relative;
        top: -2px;
    }

    &:hover {
        color: $black;
        svg path{
            fill:$black;
        }
    }
}

.form-control {
    font-size: 16px;
    line-height: 24px;
    color: #707070;

    @include media-breakpoint-up(lg) {
        padding: 17px 32px;
    }
}

.modal-content {
    background: transparent;
    border: none;

    iframe {

        @include media-breakpoint-down(lg) {
            height: 100%;
        }

    }
}

.modal-header {
    justify-content: right;
    border: none;

    @include media-breakpoint-up(xxl) {
        top: 10px;
        left: 50px;
        position: relative;
        border: none;
    }

}

.modal-header .btn-close {
    background-size: contain;
    filter: invert(97%) sepia(97%) saturate(0%) hue-rotate(296deg) brightness(107%) contrast(101%);
}


@mixin button-variant($bg, $border, $color, $bg-hover) {
    background-color: $bg;
    color: $color;
    border: none;
    padding: 20px 0;
    font-weight: bold;
    font-size: 16px;
    letter-spacing: 0.15em;
    line-height: 20px;
    text-transform: uppercase;

    @include media-breakpoint-down(lg) {
        width: 100%;
    }

    @include media-breakpoint-between(sm, lg) { 
        padding: 32.1px 0;
        font-size: 30px;
        line-height: 34px;
    }

    @include media-breakpoint-up(lg) {
        width: 365px;
        padding: 20px 0;
    }

    &:hover {
        background-color: $bg-hover;
    }
}

.btn-primary {

    @include button-variant($primary, $btn-border, $btn-color, $secondary);

    i {
        font-size: 16px;
        font-weight: bold;
        margin-right: 10px;


        @include media-breakpoint-up(sm) {
            font-size: 33px;
            margin-right: 19.8px;
        }

        @include media-breakpoint-up(xl) {
            font-size: 23px;
            margin-right: 11px;
        
        }   

    }
}

.btn-secondary {
    @include button-variant($secondary, $btn-border, $btn-color, $primary);
}

// Then add additional custom code here
.modal-dialog {

    min-width: 300px;

    @include media-breakpoint-up(sm) {
        min-width: 400px;
    }

    @include media-breakpoint-up(md) {
        min-width: 600px;
    }

    @include media-breakpoint-up(lg) {
        min-width: 800px;
    }

    @include media-breakpoint-up(xl) {
        min-width: 1025px;
    }

    @include media-breakpoint-up(xl) {
        min-width: 1200px;
    }  

}

