//MMmenu UI
.mm-menu {  


    min-width: 100% !important;
    max-width: 100% !important;
    z-index: 9 !important;
    margin-top: 75px !important;
    background: #fff !important;

    &.show {
        display: block;
    }



    @include media-breakpoint-up(sm) {
        margin-top: 130px !important;
    }

    .mm-navbar, .mm-panels, .mm-panel {
        background: #fff;
        border: none;    
    }

    // .mm-panel_opened {

    //     .mm-navbar.mm-navbar_sticky {
    //         display: none;
    //     }

    // }

    // .mm-panel_opened-parent {
    //     .mm-navbar.mm-navbar_sticky {
    //         display: block;
    //     }
    // }

    .mm-listview {

        &:after { display: none; }

        li {
            // padding: 20px 0;

            @include media-breakpoint-up(sm) {
                // padding: 56px 0;
            }

            &:after { display: none; }

            .mm-btn_next:after {
                border-color: #000;
            }

            .mm-listitem__btn {
                background: transparent;
                border: none !important;
            }

            a {
                background: transparent !important;
                border: none;
                box-shadow: none;
                color: #000 !important;
                font-weight: bold;
                font-family: "Arial";
                letter-spacing: 0.15em;
                font-size: 20px;

                @include media-breakpoint-up(sm) {
                    font-size: 30px;
                }

            }


        }

    }

    .mm-wrapper_opened .mm-panels .mm-navbar > .mm-navbar__title {
        display: none;
    }

    .mm-wrapper_opened .mm-panels .mm-navbar > a + .mm-navbar__title {
        display: flex;
        padding-top: 22px;
        padding-bottom: 22px;
        justify-content: left;
        text-transform: uppercase;

        @include media-breakpoint-up(sm) {
            padding-top: 43px;
            padding-bottom: 43px;
        }
    }

    div + .mm-panel_opened .mm-navbar.mm-navbar_sticky {
        background: #3C83BB;

        @include media-breakpoint-up(md) { 
            padding: 0 30px;
        }
    }

    div + .mm-panel_opened .mm-btn_next:after, div + .mm-panel_opened .mm-btn_prev:before {
        border-color: #fff;
    }

    div + .mm-panel_opened .mm-navbar__title>span {
        font-weight: bold;
        color: #fff;
    }

    ul li.dropdown, 
    ul li.nav-item {
        // padding-top: 20px;
        // padding-bottom: 20px;

        @include media-breakpoint-up(sm) {
            padding-top: 20px;
            padding-bottom: 20px;
        }
    }

    .mm-btn_next:after, .mm-btn_prev:before {

        @include media-breakpoint-up(sm) {

            width: 15px !important;
            height: 15px !important;
        }

    }

    .mm-navbar__title>span {
        font-size: 20px;

        @include media-breakpoint-up(sm) {
            font-size: 30px;
            line-height: 34px;
        }
    }


    #mm-0 li {
        padding-top: 5px;
        padding-bottom: 5px;
    }

    .mm-listview li > a:first-child {
        text-transform: uppercase;
        font-size: 20px;


        @include media-breakpoint-up(sm) {
            font-size: 30px;
            padding-top: 15px;
            padding-bottom: 15px;
        }

    }

    .dropdown.mm-listview li,
    .dropdown-menu .mm-listview li {
        padding-left: 0 !important;
        padding-right: 0 !important;

        @include media-breakpoint-up(sm) {
            padding: 25px 30px !important;
        }

    }

    .mm-navbar.mm-navbar_sticky {
        background: transparent;
    }

    .mm-navbar__title span {
        display: none;
    }

    .dropdown-menu .mm-navbar__title span {
        display: block !important;
        text-transform: uppercase;
        text-align: left;
    }
    
    .mm-navbar__btn:not(.mm-hidden)+.mm-navbar__title {
        font-weight: bold;
        font-size: 20px;
        padding: 20px 0;
    }

    div#mm-4 {
        display: block !important;
        visibility: visible !important;
        z-index: 99999;
        transform: none;
        top: unset;

        .mm-navbar.mm-navbar_sticky {
            display: none;
        }

        a i {
            font-size: 55px;
            color: #3C83BB;


            @include media-breakpoint-up(sm) {
                font-size: 80px;
            }

        }

        @include media-breakpoint-up(sm) {

            padding-left: 30px;
            padding-right: 30px;

        }
        
     }



}


//header
header {

    //header nav menu wrapper
    .navbar {
        box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
        background: #fff !important;
        padding: 20px 15px;

        @include media-breakpoint-up(sm) {
            padding: 47.5px 50px;
        }

        @include media-breakpoint-up(lg) {
            padding: 0 10px;
        }

        @include media-breakpoint-up(xl) {
            padding: 0 20px;
        }

        @include media-breakpoint-up(xxxl) {
            padding: 0 50px;
        }


        // Nav Toggle - Bento Menu
        .navbar-toggler {

            border: 0;
            padding-top: 0;
            padding-right: 0;

            .menu__item--bento {
                width: 35px;
                height: 30px;
                // padding: 20px;
                display: flex;
                flex-wrap: wrap;
                justify-content: center;
                align-items: center;
                cursor: pointer;
            }

            .close-icon {

                svg {
                    width: 25px;
                }

            }

            /* ditch the ugle blue outline/border/box-shadow */
            &:focus,
            &:active,
            &:hover {
                outline: 0;
                border: 0;
                box-shadow: none;

            }

        }

                
        /* hide close when bento menu shown */
        // .navbar-toggler.collapsed .close-icon {
        //     display: none;
        // }
        // .navbar-toggler .navbar-toggler-custom-icon 
        // .navbar-toggler:not(.collapsed) .close-icon { display: block;}

        // .navbar-toggler:not(.collapsed) .navbar-toggler-custom-icon {
        //     display: block;
        // }

        .menu__item--bento .box {
            width: 5px;
            height: 5px;
            border-radius: 50%;
            background: #000000;
            margin: 1px 3px;
            display: inline-block;
        }

        .navbar-collapse {

            @include media-breakpoint-down(lg) {
                display: none !important;
            }


        }

        .navbar-collapse.show {

            @include media-breakpoint-down(lg) {
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                align-items: baseline;
            }

            .list-unstyled {

                padding-top: 50px;

                a {
                    font-size: 46px;
                }

            }

        }

        .dropdown {
            position: inherit;

            >span {
                color:#000 !important;
            }
        }

        .navbar-nav {

            @include media-breakpoint-down(lg) {
                width: 100%;
            }

            .dropdown-toggle::after {
                display: none;
            }


            .dropdown > span ,
            >li > a {
                padding-top: 29.5px;
                padding-bottom: 29.5px;

            }
            
            > li > a > span {
                color: #000 !important;
                font-weight: bold;
            }

            li {

                .dropdown-menu.show {
                    top: 100%;
                    left: 0;
                    right: 0;
                }

                > span {
                    text-transform: uppercase;
                    font-weight: bold;
                    color: #000;
                    margin-right: 7px;

                    @include media-breakpoint-up(lg) {
                        font-size: 13px;
                    }

                    @include media-breakpoint-up(xxl) {
                        margin-left: 29px;
                        font-size: 14px;
                    }

                    @include media-breakpoint-down(lg) {
                        margin: 0;
                    }

                }

                @include media-breakpoint-down(lg) {
                    padding: 10px 0;
                }

                a {
                    font-weight: bold;
                    text-transform: uppercase;

                    &:hover {
                        color: #1697D9 !important;
                    }

                    @include media-breakpoint-up(lg) {
                        font-size: 13px;
                    }

                    @include media-breakpoint-up(xxl) {
                        margin-left: 29px;
                        font-size: 14px;
                    }

                    @include media-breakpoint-down(lg) {
                        margin: 0;
                    }

                    i { color: #000;  }

                    .arrow-left-icon {
                        float: left;

                        svg {
                            width: 10px;
                        }

                    }

                    .arrow-right-icon {
                        float: right;

                        svg {
                            width: 10px;
                            margin-top: -10px;
                        }

                    }
                }

                @include media-breakpoint-down(lg) {

                    span {
                        font-size: 22px;
                        color: #000 !important;
                        font-weight: bold;
                        font-family: "Arial";
                        letter-spacing: 0.15em;
                    }

                }

                .dropdown-menu {
                    background-color: rgba(255,255,255,0.75);    
                    margin-top: 0;           
                    border: none;

                    @include media-breakpoint-up(lg) {
                        padding: 25px 0 !important;
                    }

                    .submenu-title {

                        background-color: #3C83BB;
                        margin-left: -15px;
                        margin-right: -15px;

                        span {
                            color: #fff !important;
                            text-transform: uppercase;
                        }
                    }
                    


                        a {

                            font-size: 22px;
                            text-transform: uppercase;
                            color: #000 !important;
                            font-weight: bold;
                            font-family: "Arial";
                            letter-spacing: 0.15em; 

                            span {
                                margin-right: 6px;
                                text-transform: uppercase;
                            }

                            i {
                                color: #032853 !important;
                            }
                            
                            @include media-breakpoint-up(lg) {
                                font-size: 16px;
                                letter-spacing: 0;
                                color: #032853 !important;

                                &:hover {
                                    color: #1697D9 !important;  
                                }
                            }
 
                            // margin-left: 32px;
                            // margin-right: 32px;


                            @include media-breakpoint-up(xl) {
                                margin-left: 5px;
                                margin-right: 5px;
                            }

                            @include media-breakpoint-up(xxl) {
                                margin-left: 10px;
                                margin-right: 10px;                               

                            }

                            @include media-breakpoint-up(xxxl) {
                                margin-left: 32px;
                                margin-right: 32px;
                            }

                        }



                        
                }



            }



        }



        #mdb-5-search-container {

            .dropdown-toggle::after{ display: none;  }

            .input-group-text {
                background-color: transparent;
                border: none;
                cursor: pointer;
            }

            .dropdown-menu {
                background-color: rgba(255,255,255,0.75);  
                margin: 0;
                border: none;
            }

            .dropdown-menu.show {
                top: 100%;
                left: 0;
                right: 0;
            }


        }

    }

}

