//timeline type 1
.timeline {

    background-color: rgba(3, 40, 83, 0.05);
    padding-top: 75px;

    .section-content {

        p {
            padding-top: 30px;
            padding-bottom: 78px;


            @include media-breakpoint-up(sm) {
                padding-top: 60px;
                padding-bottom: 156px;
            }
    
    
            @include media-breakpoint-up(lg) {
                padding-top: 33px;
                padding-bottom: 98px;            
            }        
        }
    }

    @include media-breakpoint-up(sm) {
        padding-top: 150px;
    }


    .timeline-bar-box {

        padding-bottom: 100px;

        @include media-breakpoint-up(sm) {
            padding-bottom: 200px;
        }

        @include media-breakpoint-up(lg) {
            position: relative;
