        height: 400px;
        display: flex;
        align-items: center;
    }

    &:before {
        content: "";
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        display: block;
        background-image: linear-gradient( to right, rgba(0,0,0, 0.75), transparent);
    }

    > div {
        position: relative;
        z-index: 9;
    }   
}

